import React , { useEffect } from "react";
import ApexCharts from 'apexcharts';
import { Row, Col } from "react-bootstrap";

const Sidebar=()=>{

    useEffect(()=>{
      var name='500/8000'
      var options = {
        series: [90],
        chart: {
        type: 'radialBar',
      },
      plotOptions: {
        radialBar: {
          hollow: {
            size: '70%',
          }
        },
      },
      labels: [name],
      };

      var chart1 = new ApexCharts(document.querySelector("#chart1"), options);
      chart1.render();
      var chart2 = new ApexCharts(document.querySelector("#chart2"), options);
      chart2.render();
      var chart3 = new ApexCharts(document.querySelector("#chart3"), options);
      chart3.render();
    },[])
    

    return(
        <>
    <div className="side-bar">
    <div className="bar-box">
    <div className="bar-heading">
    <h3>Nakamoto Games</h3>  <img/>
    </div>
    <div className="bar-cnt">
    <h3> <span>$</span>51.05</h3>  
    <p>Total Raize</p>
    </div>
    </div> {/* bar box closed */}

    <div className="bar-box">
    <div className="bar-heading">
    <h3>Contributor’s statistics</h3>
    </div> 
    <div className="bar-cnt d-flex">
    <p className="tm"><i class="fa fa-circle" aria-hidden="true"></i> Total amount</p>
    <p  className="nc"><i class="fa fa-circle" aria-hidden="true"></i> Nr. of contributors</p>
    </div>
    </div> {/* bar box closed */}

    <div className="bar-box">
    <div className="bar-heading">
      <h3>Contributor’s statistics</h3> 
    </div> 
    <Row>
      <Col md={4}><div id="chart1"></div></Col>
      <Col md={4}><div id="chart2"></div></Col>
      <Col md={4}><div id="chart3"></div></Col>
    </Row>
    
    </div> {/* bar box closed */}
    </div> {/* sidebar closed */} 
    </>
    )
}
  export default Sidebar